<template>
  <div class="common-layout">
    <el-container>
      <el-aside width="200px" class="aside-layout">
        <div class="logo-layout">
          <img src="../../../public/resource/favicon.png" width="30" height="30"/>
          <el-divider direction="vertical"/>
          <Vue3Marquee pause-on-hover :duration="10" :pause="getMixLength(publishDatas.teamName) <= 13">
            <span>{{ publishDatas.teamName }}</span>
          </Vue3Marquee>
        </div>
        <el-menu
          default-active="2"
          class="el-menu-vertical"
          background-color="#f9f9f9"
          unique-opened
        >
          <el-scrollbar>
            <template v-for="menu in publishDatas.rowList" :key="menu.spaceId">
              <el-sub-menu :index="menu.spaceId" v-if="menu.folders && menu.folders.length > 0">
                <template #title>
                  <div class="flex items-center" @click="menuClick(menu.spaceId, null)">
                    <el-image class="menu-icon" :src="menu.spaceLogoUrl || ''">
                      <template #error>
                        <SvgIcon :name="menu.spaceType == 'PUBLIC' ? 'space-public' : 'space-private'" :size="20"/>
                      </template>
                    </el-image>
                    {{ menu.spaceName }}
                  </div>
                </template>
                <el-menu-item :index="folder.id" v-for="folder in menu.folders" :key="folder.id"
                              @click="menuClick(menu.spaceId, folder.id)">
                  {{ folder.name }}
                </el-menu-item>
              </el-sub-menu>
              <el-menu-item :index="menu.spaceId" @click="menuClick(menu.spaceId, null)" v-else>
                <el-image class="menu-icon" :src="menu.spaceLogoUrl || ''">
                  <template #error>
                    <SvgIcon :name="menu.spaceType == 'PUBLIC' ? 'space-public' : 'space-private'" :size="20"/>
                  </template>
                </el-image>
                <template #title>{{ menu.spaceName }}</template>
              </el-menu-item>
            </template>
          </el-scrollbar>
        </el-menu>
      </el-aside>
      <el-container class="main-container-layout">
        <el-header>
          <div class="main-top-header">
            <div class="header-menu">
              <el-button link size="large" @click="toMake">
                <SvgIcon name="menu-copy"/>&nbsp;我要制作
              </el-button>
              <el-button link size="large">
                <SvgIcon name="menu-nav-res"/>&nbsp;<a :href="RouteEnum.NAV_RES">导航大全</a>
              </el-button>
              <el-button link size="large" @click="aboutUs">
                <SvgIcon name="menu-about-us"/>&nbsp;关于我们
              </el-button>
            </div>
            <div class="header-user">
              <User nextHandle="1" v-if="hasLogin"/>
              <Unauth v-else/>
            </div>
          </div>
        </el-header>
        <el-main class="main-layout">
          <el-scrollbar>
            <WebsiteDetail v-if="navId"/>
            <SearchState :publishId="publishId" :search="search" v-else-if="search"/>
            <MainState :publish-datas="publishDatas" :key="timeRefusr" v-else/>
          </el-scrollbar>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script setup lang="ts">
import {useRoute, useRouter} from 'vue-router'
import {useTitle as useWebPageTitle} from "@vueuse/core";
import {getAppEnvConfig} from "@/utils/env/env";
import {inject, onBeforeUnmount, onMounted, ref} from "vue";
import {useUser} from "@/hooks/components/useUserLogin";
import {RouteEnum} from "@/enums/routeEnum";
import User from "@/layout/default/header/components/user-dropdown/user.vue";
import Unauth from "@/layout/default/header/components/user-dropdown/unauth.vue";
import {fetchTeamPublishDatas} from "@/api/fetchs/team";
import {Vue3Marquee} from 'vue3-marquee'
import {getMixLength, getPublishLocation, getToken} from '@/utils'
import MainState from './components/MainState.vue'
import SearchState from './components/SearchState.vue'
import WebsiteDetail from './components/WebsiteDetailState.vue'
import {useGo} from "@/hooks/web/usePage";
import {ElMessage} from "element-plus/es";
import {useUserStore} from "@/store/modules/user";
import {useEmit, useOffEmitFn, useOnEmitFn} from "@/utils/useEmitter";

const route = useRoute()
const go = useGo()
const webPageTitle = useWebPageTitle()
const {appTitle} = getAppEnvConfig()
const {hasLogin} = useUser()
const {currentRoute} = useRouter()

const publishId = route.params.publishId.indexOf('#') > 0 ? route.params.publishId.slice(0, route.params.publishId.indexOf('#')) : route.params.publishId
const navId = route.params.navId.indexOf('#') > 0 ? route.params.navId.slice(0, route.params.navId.indexOf('#')) : route.params.navId
const search = currentRoute.value.query.s
const currentHash = window.location.hash

const timeRefusr = ref(new Date().getTime())

const publishDatas = ref({})

function menuClick(spaceId, folderId) {
  // 如果这两个参数有值，说明当前页是详情页或者搜索页，点击菜单的时候做跳转操作
  if (navId || search) {
    let loc = '#row-' + spaceId
    if (folderId) {
      loc += '-' + folderId
    }
    window.open(window.location.origin + '/p/' + publishId + loc, '_self')
  } else {
    // 1.点击一级菜单（空间）要执行锚点，需要拿到spaceId
    // 2.点击二级菜单（空间或目录）要自动切换对应的tab，需要拿到spaceId 和 目录id
    const rowEle = document.querySelector("#row-" + spaceId);
    if (rowEle) {
      rowEle.scrollIntoView(true);
    }
    if (folderId) {
      useEmit('handleChangeActiveName', {spaceId, folderId})
    }
  }
}

function aboutUs() {
  go(RouteEnum.ABOUT)
}

function toMake() {
  ElMessage.info('暂未开发')
}

useAutoLogin()

function useAutoLogin() {
  let cookies = inject('$cookies')
  let token = getToken(window.location.hash)
  if (!token) {
    return
  }

  // @ts-ignore
  cookies.config("7d")
  // @ts-ignore
  cookies.set("x-access-token", token);

  const userStore = useUserStore()
  userStore.oauthLogin()
}

onMounted(() => {
  setTimeout(() => {
    if (currentHash) {
      let publishLocation = getPublishLocation(currentHash)
      if (publishLocation.spaceId) {
        menuClick(publishLocation.spaceId, publishLocation.folderId)
      }
    }
  }, 500)
})

useOnEmitFn('handleUpdateTeamPublishDatas', handleGetTeamPublishDatas)

onBeforeUnmount(() => {
  useOffEmitFn('handleUpdateTeamPublishDatas')
})

/* ================================== fetchs ================================== */
async function handleGetTeamPublishDatas() {
  const {data, status} = await fetchTeamPublishDatas(publishId)
  if (status == 1) {
    webPageTitle.value = data.teamName + ' - ' + appTitle
    publishDatas.value = data

    timeRefusr.value = new Date().getTime()
  }
}

handleGetTeamPublishDatas()
</script>

