<template>
  <div class="search-state menu-view-container">
    <SearchEngineSimple :defaultSearch="searchValue" @doSearch="doSearch"/>
    <div class="flex items-center mb-6">
      <div class="navs-item navs-item--active">
        <span>网站</span>
      </div>
    </div>
    <div class="flex items-center mb-6">
      <el-icon :size="18">
        <Search />
      </el-icon>&nbsp;
      <div style="font-size: 18px;color: #555555;">"<b>{{searchValue}}</b>"的搜索结果</div>
    </div>
    <el-row class="mb-5" :gutter="20" v-loading="loading">
      <el-col
        class="relative"
        v-for="dataItem in websites"
        :key="dataItem.id"
        :xs="12"
        :sm="12"
        :md="8"
        :lg="8"
        :xl="8"
      >
        <WebsitePublish :website-item="dataItem" :show-checkbox="false" :checked-state="false"/>
      </el-col>
    </el-row>
    <el-pagination
      v-model:current-page="currentPage"
      background
      hide-on-single-page
      layout="prev, pager, next"
      :default-page-size="pageSize"
      :total="totalCount"
      @current-change="handleCurrentChange"/>
  </div>
</template>

<script setup lang="ts">
import SearchEngineSimple from "@/components/SearchEngine/SearchEngineSimple.vue"
import { Search } from '@element-plus/icons-vue'
import WebsitePublish from '@/components/ContentElement/WebsitePublish.vue'
import {fetchSearchNavDatasPage} from "@/api/fetchs/team";
import {ref} from "vue";
import {useConst} from "@/components/ContentElement/const";

const {commonWebsiteMenuList} = useConst()

const props = defineProps<{
  publishId: string
  search?: string
}>()

const searchValue = ref<string>('')
const websites = ref<any[]>([])
const loading = ref<boolean>(false)
const totalCount = ref<number>(0)
const pageSize = ref(24)
const currentPage = ref(1)

searchValue.value = props.search || ''

function doSearch(val) {
  currentPage.value = 1
  searchValue.value = val
  handleSearchTeamPublishDatas()
}

function handleCurrentChange(val: number) {
  handleSearchTeamPublishDatas()
}

/* ================================== fetchs ================================== */
async function handleSearchTeamPublishDatas() {
  try{
    loading.value = true
    const {rows, total} = await fetchSearchNavDatasPage(props.publishId, searchValue.value, currentPage.value, pageSize.value)
    websites.value = rows
    totalCount.value = total
  } finally {
    loading.value = false
  }
}

handleSearchTeamPublishDatas()

</script>

