<template>
  <div>
    <span
      :class="[
							'interspace',
							{
								'enter-type':
									getEnterType === 'interspace' &&
									getEnterId === folderItem.id &&
									getStartItem.id !== getEnterId &&
									getStartItem.dataType !== DataTypeEnum.WEBSITE
							}
						]"
      @mousemove="handleMousemove(folderItem, $event, 'interspace')"
    />
    <div
      class="menu-folder-item menu-folder-item-publish"
      :class="{
							enter: getEnterId === folderItem.id && getStartItem.id && getStartItem.id !== getEnterId,
							opacity:
								getStartItem.id && getStartItem.id === folderItem.id && getStartItem.id !== getEnterId
						}"
      @mousedown="handleMousedown(folderItem, $event, getFolderList)"
      @mousemove="handleMousemove(folderItem, $event)"
      @mouseenter.stop.prevent="handleMouseenter(folderItem)"
      @mouseleave="handleMouseleave(folderItem)"
    >
      <div class="flex items-center">
        <div class="folder-logo">
          <SvgIcon name="list-folder" :size="45"/>
        </div>
        <div class="folder-p">
          <p :title="folderItem.name">{{ folderItem.name }}</p>
          <p>{{ folderItem.desc }}</p>
        </div>
      </div>
      <ItemDropdown
        v-if="itemMenuList"
        :itemData="folderItem"
        :ikey="folderItem.id"
        :itemMenuList="itemMenuList"
        class="menu-drop"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import ItemDropdown from './ItemDropdown.vue'
import {useMouseEvent} from "./useMouseEvent";
import {DataTypeEnum} from './types'
import {DropMenu} from "@/components/Dropdown/useDropdown";

const props = defineProps<{
  folderItem: any
  itemMenuList: DropMenu[]
  canClick: boolean
}>()

/* drag */
const {
  getStartItem,
  getEnterId,
  getEnterType,
  handleMousedown,
  handleMouseenter,
  handleMouseleave,
  handleMousemove
} = useMouseEvent()

</script>

<style lang="scss" scoped>
@import './content.scss';
</style>
