<script setup lang="ts">
import {useRoute, useRouter} from 'vue-router'
import {ref} from 'vue'
import {CollectionTag, Place, Right, Star, Timer, View} from '@element-plus/icons-vue'
import WebsitePublish from '@/components/ContentElement/WebsitePublish.vue'
import Footer from '@/layout/default/footer/index.vue'
import VueQrcode from '@chenfengyuan/vue-qrcode'
import SimpleDialog from '@/components/SimpleDialog/SimpleDialog.vue'
import CloneMove from '@/components/ContentElement/clone-move/CloneMove.vue'
import {useCloneMoveForm} from '@/components/ContentElement/clone-move/useCloneMove'
import {fetchAddOpenHistory, fetchWebsiteDetail} from "@/api/fetchs/teamNav";
import {getSearchParams} from "@/utils/form";

const router = useRouter();
const route = useRoute()

interface WebsiteInfo {
  id?: string,
  name?: string,
  url?: string,
  categoryName?: string,
  hitsNum?: number,
  collectNum?: number,
  createTime?: string,
  desc?: string,
  bigDesc?: string,
  logo?: string,
  tags?: [],
  screenshots?: [],
  relationWebsites?: []
}

const publishId = route.params.publishId.indexOf('#') > 0 ? route.params.publishId.slice(0, route.params.publishId.indexOf('#')) : route.params.publishId
const navId = route.params.navId.indexOf('#') > 0 ? route.params.navId.slice(0, route.params.navId.indexOf('#')) : route.params.navId

const websiteDetail = ref<WebsiteInfo>({})

const {cloneMoveData, handleCloneMoveVisible, handleCloneMoveRes} = useCloneMoveForm('收藏到')

async function handleCollect() {
  handleCloneMoveVisible()
}

function visitWebsite(url) {
  handleSaveOpenHistory(navId)
  window.open(url)
}

function getDomainFromUrl(url) {
  if(!url) {
    return ''
  }
  const urlObj = new URL(url);
  return urlObj.hostname;
}
function get5118Url(url) {
  url = getDomainFromUrl(url)
  return 'https://seo.5118.com/' + url + '?ref=www.navigationline.cn'
}
function getAiZhan(url) {
  url = getDomainFromUrl(url)
  return 'https://www.aizhan.com/cha/' + url + '?ref=www.navigationline.cn'
}
function getChinaz(url) {
  url = getDomainFromUrl(url)
  return 'https://seo.chinaz.com/?q=' + url + '&ref=www.navigationline.cn'
}
/* ================================== fetchs ================================== */
async function getWebsiteDetail() {
  const {data} = await fetchWebsiteDetail({publishId, navId})

  websiteDetail.value = {
    id: data.id,
    name: data.name,
    url: data.url,
    categoryName: data.categoryName,
    hitsNum: data.hitsNum,
    collectNum: data.collectNum,
    createTime: data.createTime,
    desc: data.desc,
    bigDesc: data.bigDesc,
    logo: data.logo,
    tags: data.tags,
    screenshots: data.screenshots,
    relationWebsites: data.relationWebsites
  }
}

getWebsiteDetail()

async function handleSaveOpenHistory(navId) {
  const json = {navId}
  const param = getSearchParams(json)
  await fetchAddOpenHistory(param)
}

</script>

