<template>
  <div class="menu-view-container publish-basic-row">
    <div class="row-title" :id="'row-' + rowData.id" v-if="rowData?.title">
      <el-image class="menu-icon" :src="rowData.logoUrl || ''">
        <template #error>
          <SvgIcon :name="rowData.icon" :size="20"/>
        </template>
      </el-image>&nbsp;
      <span>{{rowData.title}}</span>
    </div>
    <el-tabs
      v-model="activeName"
      class="row-tabs"
      @tab-change="tabChange"
      @tab-click="tabClick"
      v-if="rowData?.children && rowData?.children.length > 0">
      <el-tab-pane
        v-for="paneItem in rowData.children"
        :key="paneItem.title"
        :label="paneItem.title"
        :name="paneItem.id"
      >
        <el-breadcrumb :separator-icon="ArrowRight">
          <el-breadcrumb-item v-for="(bread, index) in breadcrumbItems" :key="bread.folderId" @click="index < breadcrumbItems.length -1 && handleFolderClick(bread.folderId, bread.folderName)">
            <SvgIcon name="list-folder"/>&nbsp;
            <span>{{bread.folderName}}</span>
          </el-breadcrumb-item>
        </el-breadcrumb>
        <el-row :gutter="15" v-if="paneItem.folders?.length > 0 || paneItem.websites?.length > 0">
          <el-col
            v-for="dataItem in paneItem.folders"
            :key="dataItem.id"
            class="relative"
            :xs="12"
            :sm="8"
            :md="6"
            :lg="4"
            :xl="4"
          >
            <Folder :folder-item="dataItem" :can-click="true" @click="handleFolderClick(dataItem.id, dataItem.name)"/>
          </el-col>
          <el-col
            class="relative"
            v-for="dataItem in paneItem.websites"
            :key="dataItem.id"
            :xs="12"
            :sm="8"
            :md="6"
            :lg="4"
            :xl="4"
          >
            <WebsitePublish :website-item="dataItem" :show-checkbox="false" :isCommonUse="isCommonUse" :checked-state="false"/>
          </el-col>
        </el-row>
        <div class="empty-data" v-else>
          <template v-if="!loading">
            <SvgIcon name="no-data" :size="16"/>&nbsp;
            <span>暂无数据</span>
          </template>
          <template v-else>
            <el-icon class="is-loading">
              <Loading />
            </el-icon>&nbsp;
            <span>加载中</span>
          </template>
        </div>
      </el-tab-pane>
    </el-tabs>
    <el-row :gutter="15" v-if="rowData?.websites">
      <el-col
        class="relative"
        v-for="dataItem in rowData.websites"
        :key="dataItem.id"
        :xs="12"
        :sm="8"
        :md="6"
        :lg="4"
        :xl="4"
      >
        <WebsitePublish :website-item="dataItem" :show-checkbox="false" :isCommonUse="isCommonUse" :checked-state="false"/>
      </el-col>
    </el-row>
  </div>
</template>

<script setup lang="ts">
import {onBeforeUnmount, reactive, ref} from "vue";
import WebsitePublish from '@/components/ContentElement/WebsitePublish.vue'
import Folder from '@/components/ContentElement/FolderPublish.vue'
import {fetchNavDatas} from "@/api/fetchs/team";
import { Loading, ArrowRight } from '@element-plus/icons-vue'
import {useOffEmitFn, useOnEmitFn} from "@/utils/useEmitter";

interface BasicRow {
  id?: string
  title?: string
  logoUrl?: string
  icon?: string
  folders?: any[]
  websites?: any[]
  // 二级标题
  children?: BasicRow[]
}

const props = defineProps<{
  // 一级标题
  basicRow: BasicRow,
  isCommonUse?: boolean
}>()

const activeName = ref<string>('')
const initedTab = reactive<string[]>([])
const rowData = ref<BasicRow>({})
const loading = ref<boolean>(false)
const breadcrumbItems = ref<any[]>([])

function initActiveName() {
  rowData.value = props.basicRow

  if(props.basicRow?.children && props.basicRow.children.length > 0) {
    activeName.value = props.basicRow.children[0].id as string
    initedTab.push(activeName.value)
  }
}

initActiveName()

function handleChangeActiveName(param) {
  let spaceId = param.spaceId
  let folderId = param.folderId
  if(props.basicRow?.id == spaceId) {
    activeName.value = folderId
  }
}

function tabChange(folderId) {
  breadcrumbItems.value = []
  if(!initedTab.includes(folderId)) {
    handleGetNavDatas(folderId)
    initedTab.push(folderId)
  }
}

function tabClick() {
  if(breadcrumbItems.value.length > 0) {
    breadcrumbItems.value = []
    handleGetNavDatas(activeName.value)
    initedTab.push(activeName.value)
  }
}

useOnEmitFn('handleChangeActiveName', handleChangeActiveName)

onBeforeUnmount(() => {
  useOffEmitFn('handleChangeActiveName')
})

/* ================================== fetchs ================================== */

async function handleGetNavDatas(folderId) {
  try {
    loading.value = true
    const {data} = await fetchNavDatas(folderId)
    let tabpaneData = rowData.value.children?.find(item => item.id === folderId)
    if(tabpaneData) {
      tabpaneData['folders'] = data.folders
      tabpaneData['websites'] = data.files
    }
  } finally {
    loading.value = false
  }
}

async function handleFolderClick(folderId, folderName) {
  try {
    loading.value = true

    let tabpaneData = rowData.value.children?.find(item => item.id === activeName.value)

    const hasIn = breadcrumbItems.value.some(v => v.folderId === folderId)

    if(!hasIn) {
      if(breadcrumbItems.value.length == 0) {
        breadcrumbItems.value.push({folderId: tabpaneData?.id, folderName: tabpaneData?.title})
      }
      breadcrumbItems.value.push({folderId, folderName})
    } else {
      const index = breadcrumbItems.value.findIndex(v => v.folderId === folderId)
      breadcrumbItems.value.splice(index + 1)

      if(breadcrumbItems.value.length == 1) {
        breadcrumbItems.value = []
      }
    }

    const {data} = await fetchNavDatas(folderId)

    if(tabpaneData) {
      tabpaneData['folders'] = data.folders
      tabpaneData['websites'] = data.files
    }
    let index = initedTab.indexOf(activeName.value)
    if(index > -1) {
      initedTab.splice(index, 1)
    }
  } finally {
    loading.value = false
  }
}

</script>

