    }

    span {
      font-size: 20px;
      color: #555555;
    }
  }

  .row-tabs {
    --el-tabs-header-height: 30px;

    .el-tabs__nav-wrap::after {
      height: 0;
    }

    .el-tabs__nav {
      background-color: #e0e0e0;
      border-radius: 30px;
      padding: 3px;
    }

    .el-tabs__active-bar {
      height: 0;
    }

    .el-tabs__item.is-active {
      background-color: #5961f9;
      color: #ffffff;
      border-radius: 30px;
    }

    .el-tabs__item:hover {
      background-color: #5961f9;
      color: #ffffff;
      border-radius: 30px;
    }

    .el-tabs__item {
      padding: 0 10px !important;
      color: #888888;
    }

    .empty-data {
      height: 80px;
      background-color: #e2e2e2;
      border-radius: 5px;
      color: #888888;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .el-breadcrumb {
      margin-bottom: 10px;

      .el-breadcrumb__item {
        cursor: pointer;
      }
    }

    .el-breadcrumb__inner {
      display: flex;
