<template>
  <div class="simple-search-container h-full">
    <div class="resource-header flex flex-col justify-center items-center">
      <div class="resource-search">
        <el-input
          size="large"
          v-model="search"
          clearable
          :trigger-on-focus="false"
          :placeholder="placeholder"
          @keyup.enter.native="handleSearch"
        >
          <template #append>
            <el-button @click="handleSearch">
              <SvgIcon name="handle-query-search" :size="20" class="handle-query-search"/>
            </el-button>
          </template>
        </el-input>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import {ref} from 'vue'

const props = defineProps<{
  defaultSearch?: string
}>()

const emit = defineEmits(['doSearch'])

const search = ref<string>('')
const placeholder = ref<string>('输入关键字搜索')

search.value = props.defaultSearch || ''

function handleSearch() {
  emit('doSearch', search.value)
}
</script>

