  .resource-header {
    height: $--simple-search-header-height;

    .resource-search {
      width: 800px;

      .handle-query-search {
        color: $--white-color;
      }

      .el-input-group__append {
        background-color: var(--el-color-primary);
        box-shadow: 0 1px 0 0 var(--el-color-primary) inset,
        0 -1px 0 0 var(--el-color-primary) inset, -1px 0 0 0 var(--el-color-primary) inset;

        .el-button {
          margin: 0 -10px;
          padding: 5px 15px;
        }
      }

      .el-input--large {
        --el-input-height: 50px;
      }
    }
  }
}
</style>
