<template>
  <SearchEngine/>
  <div class="main-content-layout">
    <PublishBasicRow :basicRow="myCommonUse" :isCommonUse="true" v-if="myCommonUse"/>
    <PublishBasicRow :basicRow="mySpace" v-if="mySpace"/>
    <PublishBasicRow :basicRow="space(row)" v-for="row in publishDatas.rowList" :key="row.spaceId" :ref="row.spaceId"/>
  </div>
</template>

<script setup lang="ts">
import PublishBasicRow from '@/components/PublishBasicRow/PublishBasicRow.vue'
import SearchEngine from "@/components/SearchEngine/SearchEngine.vue"
import {computed} from "vue";

const props = defineProps<{
  publishDatas: any
}>()

const myCommonUse = computed(() => {
  // @ts-ignore
  let fileList = props.publishDatas.myCommonUseFileList
  return convertToMyCommonUse(fileList)
})

const mySpace = computed(() => {
  // @ts-ignore
  let fileList = props.publishDatas.mySpaceRow
  return convertToSpace(fileList)
})

const space = computed(() => (fileList) => {
  return convertToSpace(fileList)
})

function convertToMyCommonUse(myCommonUseFileList) {
  if (isEmpty(myCommonUseFileList)) {
    return null
  }
  return {children: [{id: 'myCommonUse', title: '我的常用', websites: myCommonUseFileList}]}
}

function convertToSpace(row) {
  if (!row || (isEmpty(row.folders) && isEmpty(row.files))) {
    return null
  }

  let icon = row.spaceType == 'MINE' ? 'menu-self-file' : row.spaceType == 'PUBLIC' ? 'space-public' : 'space-private'

  let space = {id: row.spaceId, title: row.spaceName, logoUrl: row.spaceLogoUrl, icon}

  if (row.folders && row.folders.length > 0) {
    // 仅把files放到第一个folder下（约定）
    let children = row.folders.map((folder, index) => {
      return {id: folder.id, title: folder.name, websites: index === 0 ? row.files : null}
    })
    space['children'] = children
  } else {
    space['websites'] = row.files
  }

  return space
}

function isEmpty(arr) {
  if (!arr) {
    return true
  }
  if (arr.length == 0) {
    return true
  }
  return false
}
</script>

<style scoped lang="scss">
.main-content-layout {
  padding: 10px;
}
</style>
