<template>
  <div>
    <span
      :class="[
								'interspace',
								{
									'enter-type':
										getEnterType === 'interspace' &&
										getEnterId === websiteItem.id &&
										getStartItem.id !== getEnterId
								}
							]"
    />
    <a
      class="menu-website-item"
      :class="{
								'menu-website-item--disabled': checkedState,
								enter:
									getEnterId &&
									getEnterId === websiteItem.id &&
									getStartItem.id &&
									getStartItem.id !== getEnterId &&
									getStartItem.dataType !== DataTypeEnum.WEBSITE,
								opacity:
									getStartItem.id && getStartItem.id === websiteItem.id && getStartItem.id !== getEnterId
							}"
      style="display: block;"
      :href="getWebsiteDetailUrl(websiteItem)"
    >
      <div class="flex items-center">
        <div class="website-logo">
          <el-image :src="websiteItem.logo">
            <template #error>
              <SvgIcon name="list-website" :size="45"/>
            </template>
          </el-image>
        </div>
        <div class="website-p">
          <p :title="websiteItem.name">{{ websiteItem.name }}</p>
          <p>{{ websiteItem.desc }}</p>
        </div>
      </div>
      <ItemDropdown
        :itemData="websiteItem"
        :ikey="websiteItem.id"
        :itemMenuList="publishWebsiteMenuList(true, isCommonUse)"
        cloneIsCollect
        :class="['menu-drop', { 'menu-drop--disabled': checkedState}]"
      />
      <div @click.stop v-if="showCheckbox">
        <el-checkbox
          :class="[
                    'item-checkbox',
                    { 'item-opc-checkbox': checkedState }
                  ]"
          :label="websiteItem.id"
        >{{ '' }}
        </el-checkbox>
      </div>
    </a>
  </div>
</template>

<script setup lang="ts">
import ItemDropdown from './ItemDropdown.vue'
import {useMouseEvent} from "./useMouseEvent";
import {DataTypeEnum} from './types'
import {DropMenu} from "@/components/Dropdown/useDropdown";
import {useConst} from "@/components/ContentElement/const";
import {useRoute} from "vue-router";

const props = defineProps<{
  websiteItem: any
  itemMenuList: DropMenu[]
  showCheckbox: boolean
  checkedState: boolean
  isCommonUse?: boolean
}>()

const route = useRoute()

/* drag */
const {
  getStartItem,
  getEnterId,
  getEnterType,
} = useMouseEvent()

const {publishWebsiteMenuList} = useConst()

// 点击
function getWebsiteDetailUrl(item) {
  const publishId = route.params.publishId.indexOf('#') > 0 ? route.params.publishId.slice(0, route.params.publishId.indexOf('#')) : route.params.publishId
  return window.location.origin + '/p/' + publishId + '/' + item.id
}

</script>

<style lang="scss" scoped>
@import './content.scss';
</style>
